import searchView from '@/components/search/search';
import goodsItemView from '@/components/goods/item';
import shareView from '@/components/share/share';
export default {
	data() {
		return {
			searchValue: '',
			isShow: 0,
			hidden: 0,
			lists: [],
			classid: 0,
			searchType: 'input',
			start: 0
		}
	},
	components:{searchView,goodsItemView,shareView},
	onLoad(e) {
		this.searchValue = e.value;
		this.classid = e.classid;
		console.log(e)
		uni.showLoading({
			title: '加载中'
		});
		this.onReachBottomData();
	},
	onReachBottom(e) {
		this.start += 20;
		this.onReachBottomData();
	},	
	methods: {
		onReachBottomData (e) {
			const t = this;
			if(t.classid == '') return;
			
			uni.request({
				url: t.$serverUrl + '/byclass',
				data: { 
					appkey: t.$appkey,
					classid: t.classid,
					start: t.start,
					num: 20
				},
				success: (ret) => {
					const data = ret.data.result;
					t.lists = t.lists.concat(data.list);
					uni.hideLoading();
					t.hidden = 1;
					if(data.length==0) t.isShow = !0;
				}
			});
		},
		getData (e) {
			var t = this;
			t.classid = '';
			uni.request({
				url: t.$serverUrl + '/search',
				data: { 
					appkey: t.$appkey,
					keyword: e,
					num: 60
				},
				success: (ret) => {
					const data = ret.data.result.list;
					uni.hideLoading();
					t.hidden = 1;
					if(data.length==0) {
						return t.isShow = !0;
					} else {
						t.isShow = !1
						t.lists = data;
					}
				}
			});
			
		},
	},
	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			return {
				title: '印记菜谱',
				desc: "学菜谱,就用印记菜谱,厨房小能手就是你~~~",
				success: res => {},
				fail: err => {}
			}
		}
		return {
			title: '印记菜谱',
			desc: "学菜谱,就用印记菜谱,厨房小能手就是你~~~",
			success: res => {},
			fail: err => {}
		}
	}
}
